<template>
  <div id="info-wrapper">
    <div class="info-panel-wrapper">
      <span>
        <h2>For Company</h2>
      </span>
      <div class="step-wrapper">
        <el-steps :active="active" finish-status="success">
          <el-step title=""></el-step>
          <el-step title=""></el-step>
          <el-step title="JD"></el-step>
        </el-steps>
      </div>

      <el-tabs class="form-wrapper" v-model="activeName">
        <el-tab-pane label="" name="first">
          <div class="form-wrapper">
            <el-form
              :model="firmForm"
              :rules="rules"
              ref="firmForm"
              label-width="100px"
              class="demo-firmForm"
            >
              <el-form-item label="" prop="name">
                <el-input v-model="firmForm.name"></el-input>
              </el-form-item>
              <el-form-item label="" prop="email">
                <el-input v-model="firmForm.email"></el-input>
              </el-form-item>
              <el-form-item label="" prop="phone">
                <el-input v-model="firmForm.phone"></el-input>
              </el-form-item>
              <el-form-item label="" prop="region">
                <el-input v-model="firmForm.region"></el-input>
              </el-form-item>
              <el-form-item label="" prop="intro">
                <el-input v-model="firmForm.intro"></el-input>
              </el-form-item>
              <el-form-item label="Logo" prop="intro">
                <el-input v-model="firmForm.logo"></el-input>
              </el-form-item>

              <el-form-item>
                <el-button type="primary" @click="resetForm('firmForm')"></el-button>
              </el-form-item>
            </el-form>
          </div>
        </el-tab-pane>
        <el-tab-pane label="" name="second">
          <div class="form-wrapper">
            <el-form
              :model="firmForm"
              :rules="rules"
              ref="firmForm"
              label-width="100px"
              class="demo-firmForm"
            >
              <el-form-item label="" prop="type">
                <el-select v-model="firmForm.type" placeholder="">
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="A" value="A"></el-option>
                  <el-option label="B" value="B"></el-option>
                  <el-option label="C" value="C"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="" prop="num">
                <el-input v-model="firmForm.num"></el-input>
              </el-form-item>
              <el-form-item label="" prop="brief">
                <el-input type="textarea" v-model="firmForm.brief"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="resetForm('firmForm')"></el-button>
              </el-form-item>
            </el-form>
          </div>
        </el-tab-pane>
        <el-tab-pane label="" name="three">
          <div style="width: 20%; margin: auto;">
            <el-button type="primary" @click="beforesubmit"></el-button>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
    <div class="bg-1"></div>
    <div class="bg-2"></div>

    <!--  -->
    <el-dialog title="" :visible.sync="submitDialog" width="30%" :before-close="handleClose">
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="unsubmit"> </el-button>
        <el-button type="primary" @click="submit"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "firmInfo",
  data() {
    return {
      activeName: "first",
      postData: {},

      //----+
      firmForm: {
        cid: null,
        name: "",
        email: "",
        phone: "",
        region: "",
        intro: "",
        type: "",
        num: "",
        brief: "",
        logo: ""
      },
      rules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 1,
            message: "",
            trigger: "blur"
          }
        ],
        email: [
          {
            required: true, //
            message: "", //
            trigger: "blur" //blur
          },
          {
            type: "email", //numberemaildate
            message: "",
            trigger: ["blur", "change"]
          }
        ],
        phone: [
          {
            required: true,
            pattern: /^1[34578]\d{9}$/,
            message: "",
            trigger: "blur"
          }
        ],
        region: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      //------end ------
      active: 0, //step
      labelPosition: "right",
      //-----
      ExpDialog: false,
      workExpItems: [],
      formLabelWidth: "120px",
      //----
      submitDialog: false
    };
  },
  methods: {
    //---
    beforesubmit() {
      this.submitDialog = true;
      this.active = 3;
    },
    //dialog
    submit() {
      this.submitDialog = false;
      this.active = 3;

      console.log(this.firmForm);

      this.axios({
        method: "post",
        url: "firmInfo",
        data: this.firmForm
      })
        .then(res => {
          if (res.data == 1) {
            this.$message("...");
            setTimeout(() => {
              this.$router.push({ path: "/about" });
            }, 3000);
          } else {
            this.$message.error("");
          }
        })
        .catch(error => {
          this.$message.error("");
        });
    },
    //
    unsubmit() {
      this.submitDialog = false;
      this.active = 2;
    },
    handleClose(done) {
      this.$confirm("")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  },
  watch: {
    activeName: function(val) {
      if (val === "three") {
        this.active = 2;
      } else {
        this.active = 1;
      }
    }
  },
  created(){
     if (localStorage.getItem("auth") !== null) {
      this.firmForm.cid = JSON.parse(localStorage.getItem("auth")).id;
    }
  }
};
</script>

<style>
.form-wrapper {
  margin: auto;
  width: 600px;
}
.step-wrapper {
  padding: 2% 0 2% 0;
  width: 300px;
  margin: auto;
}
#info-wrapper {
  overflow: hidden;
}
.bg-1,
.bg-2 {
  width: 100%;
  height: 360px;
}
.bg-1 {
  background: #3f617f;
}
#left,
#right {
  padding: 5%;
}
.info-panel-wrapper {
  position: absolute;
  width: 50%;
  height: 100%;
  margin: auto;
  left: 0;
  right: 0;
  top: 40px;
  background: white;
  border: solid #bbbbbb 0.3px;
}

.time {
  font-size: 13px;
  color: #999;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  padding: 0;
  float: right;
}

.image {
  width: 100%;
  height: 255px;
  display: block;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both;
}
.el-input input {
  width: 200px;
}
.text {
  font-size: 14px;
}

.item {
  margin-bottom: 18px;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}

.box-card {
  width: 480px;
  margin-bottom: 30px;
}
</style>



