<template>
  <div id="info-wrapper">
    <div class="info-panel-wrapper">
      <div class="step-wrapper">
        <el-steps :active="active" finish-status="success">
          <el-step title=""></el-step>
          <el-step title=""></el-step>
          <el-step title=""></el-step>
        </el-steps>
      </div>

      <el-tabs class="form-wrapper" v-model="activeName">
        <el-tab-pane label="" name="first">
          <div class="form-wrapper">
            <el-form
              :model="ruleForm"
              :rules="rules"
              ref="ruleForm"
              label-width="100px"
              class="demo-ruleForm"
            >
              <el-form-item label="" prop="name">
                <el-input v-model="ruleForm.name"></el-input>
              </el-form-item>
              <el-form-item label="" prop="email">
                <el-input v-model="ruleForm.email"></el-input>
              </el-form-item>
              <el-form-item label="" prop="phone">
                <el-input v-model="ruleForm.phone"></el-input>
              </el-form-item>
              <el-form-item label="" prop="region">
                <el-input v-model="ruleForm.region"></el-input>
              </el-form-item>
              <el-form-item label="" prop="school">
                <el-input v-model="ruleForm.school"></el-input>
              </el-form-item>
              <el-form-item label="" prop="degree">
                <el-select v-model="ruleForm.degree" placeholder="">
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="" prop="age">
                <el-input v-model="ruleForm.age"></el-input>
              </el-form-item>
              <el-form-item label="" prop="self">
                <el-input type="textarea" v-model="ruleForm.self"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="resetForm('ruleForm')"></el-button>
              </el-form-item>
            </el-form>
          </div>
        </el-tab-pane>
        <el-tab-pane label="" name="second">
          <div class="form-wrapper">
            <el-form
              :model="ruleForm"
              :rules="rules"
              ref="ruleForm"
              label-width="100px"
              class="demo-ruleForm"
            >
              <el-form-item label="" prop="occupation">
                <el-input v-model="ruleForm.occupation"></el-input>
              </el-form-item>
              <el-form-item label="" prop="pay">
                <el-input v-model="ruleForm.pay"></el-input>
              </el-form-item>
              <el-form-item label="" prop="type">
                <el-select v-model="ruleForm.type" placeholder="">
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                </el-select>
              </el-form-item>

              <el-form-item label="" prop="status">
                <el-input type="textarea" v-model="ruleForm.status"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="resetForm('ruleForm')"></el-button>
              </el-form-item>
            </el-form>
          </div>
        </el-tab-pane>

        <el-tab-pane label="" name="third">
          <div v-if="workExpItems.length!==0">
            <el-card :key="item.name" v-for="item in workExpItems" class="box-card">
              <div slot="header" class="clearfix">
                <span>{{item.name}}</span>
                <span>{{item.opcdate}}</span>
                <span>{{item.type}}</span>
              </div>
              <div class="text item">
                <div>{{item.dep}}{{item.ocp}}</div>
                <div>{{item.text}}</div>
              </div>
            </el-card>
          </div>

          <el-button type="primary" @click="openExpDialog" icon="el-icon-circle-plus-outline"></el-button>
        </el-tab-pane>

        <el-tab-pane label="" name="five">
          <div style="width: 20%; margin: auto;">
            <el-button type="primary" @click="beforesubmit"></el-button>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
    <div class="bg-1"></div>
    <div class="bg-2"></div>

    <!--  -->
    <el-dialog title="" :visible.sync="ExpDialog">
      <el-form :model="form">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.type" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.ocp" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.dep" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <div class="block">
            <span class="demonstration">-</span>
            <el-date-picker
              v-model="form.opcdate"
              type="monthrange"
              value-format="yyyy-MM"
              range-separator=""
              start-placeholder=""
              end-placeholder=""
            ></el-date-picker>
          </div>
          <el-form-item label="" prop="text">
            <el-input type="textarea" v-model="form.text"></el-input>
          </el-form-item>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="ExpDialog = false"> </el-button>
        <el-button type="primary" @click="addExp"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="submitDialog" width="30%" :before-close="handleClose">
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="unsubmit"> </el-button>
        <el-button type="primary" @click="submit"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "userResume",
  data() {
    return {
      auth: null,
      activeName: "first",
      postData: {},
      //----+
      ruleForm: {
        uid: null,
        name: "",
        email: "",
        phone: "",
        region: "",
        school: "",
        degree: "",
        age: "",
        self: "",
        occupation: "",
        type: "",
        status: "",
        pay: ""
      },
      rules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 1,
            message: "",
            trigger: "blur"
          }
        ],
        email: [
          {
            required: true, //
            message: "", //
            trigger: "blur" //blur
          },
          {
            type: "email", //numberemaildate
            message: "",
            trigger: ["blur", "change"]
          }
        ],
        phone: [
          {
            required: true,
            pattern: /^1[34578]\d{9}$/,
            message: "",
            trigger: "blur"
          }
        ],
        region: [
          { required: true, message: "", trigger: "blur" }
        ],
        desc: [{ message: "" }]
      },
      //------end ------
      active: 0, //step
      labelPosition: "right",
      //-----
      ExpDialog: false,
      workExpItems: [],
      form: {
        uid: null,
        name: "",
        type: "",
        ocp: "",
        dep: "",
        opcdate: "",
        text: ""
      },
      formLabelWidth: "120px",
      //----
      submitDialog: false
    };
  },
  created() {
    if (localStorage.getItem("auth") !== null) {
      this.auth = JSON.parse(localStorage.getItem("auth"));
      this.ruleForm.uid = this.auth.id;
    }
  },
  methods: {
    //---
    beforesubmit() {
      this.submitDialog = true;
      this.active = 3;
    },
    //dialog
    submit() {
      const itemData = {
        resume: this.ruleForm,
        workExps: this.workExpItems
      };

      this.submitDialog = false;
      this.active = 3;
      this.axios({
        method: "post",
        url: "/resume",
        data: itemData
      })
        .then(response => {
          if (response.data === 1) {
            this.$message("");
          } else {
            this.$message.error("");
          }
        })
        .catch(error => {
          this.$message.error("");
        });
    },
    //
    unsubmit() {
      this.submitDialog = false;
      this.active = 2;
    },
    handleClose(done) {
      this.$confirm("")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },
    //---dialog
    addExp() {
      const item = {
        uid: this.auth.id,
        name: this.form.name,
        type: this.form.type,
        ocp: this.form.ocp,
        dep: this.form.dep,
        opcdate: this.form.opcdate[0] + "" + this.form.opcdate[0],
        text: this.form.text
      };
      this.ExpDialog = false;
      this.workExpItems.push(item);
    },
    openExpDialog() {
      if (this.workExpItems.length < 3) {
        this.ExpDialog = true;
        this.form.name = "";
        this.form.type = "";
        this.form.ocp = "";
        this.form.dep = "";
        this.form.opcdate = "";
        this.form.text = "";
      } else {
        this.$message.error("");
      }
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  },
  watch: {
    activeName: function(val) {
      if (val === "five") {
        this.active = 2;
      } else {
        this.active = 1;
      }
    }
  }
};
</script>

<style>
.form-wrapper {
  margin: auto;
  width: 600px;
}
.step-wrapper {
  padding: 2% 0 2% 0;
  width: 300px;
  margin: auto;
}
#info-wrapper {
  overflow: hidden;
}
.bg-1,
.bg-2 {
  width: 100%;
  height: 360px;
}
.bg-1 {
  background: #3f617f;
}
#left,
#right {
  padding: 5%;
}
.info-panel-wrapper {
  position: absolute;
  width: 50%;
  height: 100%;
  margin: auto;
  left: 0;
  right: 0;
  top: 40px;
  background: white;
  border: solid #bbbbbb 0.3px;
}

.time {
  font-size: 13px;
  color: #999;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  padding: 0;
  float: right;
}

.image {
  width: 100%;
  height: 255px;
  display: block;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both;
}
.el-input input {
  width: 200px;
}
.text {
  font-size: 14px;
}

.item {
  margin-bottom: 18px;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}

.box-card {
  width: 480px;
  margin-bottom: 30px;
}
</style>


